@import url(https://fonts.googleapis.com/css?family=Signika:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  text-align: center;
}

body {
    text-align: center;
    font-family: 'Signika', sans-serif;
    background: #C6FFDD;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #f7797d, #FBD786, #C6FFDD);  /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-gradient(linear, left top, right top, from(#f7797d), color-stop(#FBD786), to(#C6FFDD));
    background: -webkit-linear-gradient(left, #f7797d, #FBD786, #C6FFDD);
    background: linear-gradient(to right, #f7797d, #FBD786, #C6FFDD); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.tittle_userData {
    width: 500px;
}

.form_userData {
    width: 400px;
    padding: 20px;
    font-family: 'Roboto', sans-serif;
    box-shadow: 12px 12px 40px -12px rgba(0, 0, 0, 0.8);
    border-radius: 8px;
    text-align: center;
}

.btn_userData {
    margin-top: 20px;
    width: 120px;
    background: #847179;
    color: white;
    padding: 10px;
    border-radius: 6px;
    font-size: 15px;
    font-weight: bold;
}

::-webkit-input-placeholder {
    text-align: center;
}

::-moz-placeholder {
    text-align: center;
}

:-ms-input-placeholder {
    text-align: center;
}

::-ms-input-placeholder {
    text-align: center;
}

::placeholder {
    text-align: center;
}

.user_results {
    margin: 50px;
    padding: 10px;
    background: #d0d3b8;
    font-size: 19px;
    font-weight: bold;
    color: rgb(73, 66, 66);
    box-shadow: 12px 12px 40px -12px rgba(121, 100, 124, 0.8);
    line-height: 30px;
}

.box_resuls {
    margin: 15px;
}

